		this.opcionesIdioma[this.registry.get(Variables.IdiomaSeleccionado)].setTexture(AtlasImagenes.Botones, AtlasImagenesDetalles[AtlasImagenes.Botones].callbackObtenerFrame(AtlasBotonesImagenes.LineaCirculoOn));
