		this.add.text(xPositionObjetos + 200, yPositionObjetos + 41, '2')
			.setOrigin(0, 0)
			// .setAlign('left')
			.setFontFamily("VAGRoundedBT")
			.setFontSize(16)
			.setFontStyle('bold')
			.setStroke('#000000', 3)
			.setFill('#ffffff');

		this.opcionesEfectoGanador.push(this.add.sprite(xPositionObjetos + 220, yPositionObjetos + 40, AtlasImagenes.Botones, AtlasImagenesDetalles[AtlasImagenes.Botones].callbackObtenerFrame(AtlasBotonesImagenes.LineaCirculoOff))
			.setOrigin(0, 0)
			.setInteractive({ useHandCursor: true })
			.on(Phaser.Input.Events.POINTER_UP, () => {
				this.seleccionarEfectoGanador(EfectoGanador.Espacio);
			}));
		this.add.text(xPositionObjetos + 250, yPositionObjetos + 41, '3')
			.setOrigin(0, 0)
			// .setAlign('left')
			.setFontFamily("VAGRoundedBT")
			.setFontSize(16)
			.setFontStyle('bold')
