import Phaser from "phaser";
import { Imagenes } from "../Utilidades/Diccionario";

export class LluviaEstrellas {
	public x: Phaser.Types.GameObjects.Particles.EmitterOpOnEmitType;
	public y: Phaser.Types.GameObjects.Particles.EmitterOpOnEmitType;
	public quantity: Phaser.Types.GameObjects.Particles.EmitterOpOnEmitType;

	constructor(private escena: Phaser.Scene, private depth = 0) {
		this.x = {min: 0, max: this.escena.cameras.main.width}
		this.y = {min: 0, max: 0};
		this.quantity = 1;
	}

	public create(): void {
		const particulasEstrellas = this.escena.add.particles(Imagenes.Estrella).setDepth(this.depth);
		// particulas.setScale(0.2);
		particulasEstrellas.createEmitter({
			x: this.x,
			y: this.y,
			// frame: {min: 15, max: 17},
			// frame: [1, 14, 15,16,17],
			quantity: this.quantity,
			frequency: 150,
			angle: { min: 0, max: 0 },
			gravityY: 300,
			gravityX: 0,
			// scale: {start: 0.9, end: 0.7},
			// scale: {min: 0.1, max: 0.3},
			scale: { random: [0.2, 0,99] },
			lifespan: { min: 1000, max: 2000 },
			// accelerationY: 40,
			alpha: {random: [0.2, 0.6]},
			speedX: {min: -5, max: 5},
			speedY: {min: 300, max: 400},
		});
	}
}
