		// this.monedaSprite.y = this.y;

		this.numeroSprite = this.escena.add.sprite(this.x, this.y, AtlasImagenes.Bolas, AtlasImagenesDetalles[AtlasImagenes.Bolas].callbackObtenerFrame(numeroSorteado))
			.setDisplaySize(166, 177);

