	private crear() {
		const imagenJuego = {
			1: () => Imagenes.Game1,
			2: () => Imagenes.Game2,
			3: () => Imagenes.Game3,
		};

		this.escena.add.image(this.x, this.y + 50, Imagenes.BackgroundJuego)
			.setDepth(this.manejarDepth.Fondo)
			.setOrigin(0, 0)
			.setDisplaySize(285, 260);

		// const burbuja = this.escena.add.image(this.x, this.y, Imagenes.Burbuja);
		this.escena.add.image(this.x + 70, this.y, imagenJuego[this.numeroJuego]())
			.setDepth(this.manejarDepth.TituloJuego)
			.setOrigin(0, 0);

		this.escena.add.image(this.x + 30, this.y + 130, Imagenes.Simbolos)
			.setDepth(this.manejarDepth.Simbolos)
			.setOrigin(0, 0)
			.setDisplaySize(70, 100);

		this.estrellaRoja = this.escena.add.sprite(this.x + 180, this.y + 180, Imagenes.EstrellaRoja)
			// .setOrigin(0, 0)
			.setDepth(this.manejarDepth.Estrella)
			.setDisplaySize(160, 160);

		this.cuadroGanadorArriba = this.escena.add.image(this.coordenasCuadroGanador[0].x, this.coordenasCuadroGanador[0].y, Imagenes.GanadorArriba)
			.setDepth(this.manejarDepth.CuadroGanador)
			.setOrigin(0, 0)
			.setAlpha(0)
			.setVisible(false)
			.setDisplaySize(79, 83);

		this.cuadroGanadorMedio = this.escena.add.image(this.coordenasCuadroGanador[1].x, this.coordenasCuadroGanador[1].y, Imagenes.GanadorMedio)
			.setDepth(this.manejarDepth.CuadroGanador)
			.setOrigin(0, 0)
			.setAlpha(0)
			.setVisible(false)
			.setDisplaySize(68, 86);

		this.cuadroGanadorAbajo = this.escena.add.image(this.coordenasCuadroGanador[2].x, this.coordenasCuadroGanador[2].y, Imagenes.GanadorAbajo)
			.setDepth(this.manejarDepth.CuadroGanador)
			.setOrigin(0, 0)
			.setAlpha(0)
			.setVisible(false)
			.setDisplaySize(83, 87);

		// this.textoTitulo = this.escena.add.text(0, 0, ObtenerTextoMultiIdioma(this.escena.registry.get(Variables.IdiomaSeleccionado), TextosAplicacion.MainGameScene_ApuestaPorLinea))
		// 	.setDepth(manejarDepthMainGame.profundidad4)
		// 	// .setAlign('left')
		// 	.setFontFamily('VAGRoundedBT')
		// 	.setFontSize(28)
		// 	// .setFontStyle('bold')
		// 	.setStroke('#c6830c', 3)
		// 	.setFill('#ffffff');
		// Phaser.Display.Align.In.Center(this.textoTitulo, this.botonCajaCentral, 0, (this.y - this.botonCajaCentral.y) - 10);
		// Phaser.Display.Align.In.Center(this.textoTitulo, this.botonCajaCentral, 0, (this.botonCajaCentral.displayHeight / 2) - (this.botonCajaCentral.displayHeight * 0.05));
		// Phaser.Display.Align.In.TopCenter(this.textoTitulo, this.botonCajaCentral,0 , 8);
	}

