	private animacionTerminoLanzamiento() {
		this.escena.tweens.add({
			targets: [this.cuadroGanadorArriba, this.cuadroGanadorMedio, this.cuadroGanadorAbajo],
			duration: 1000,
			alpha: 1,
			onStart: () => {
				//
			},
			onComplete: () => {
				//
			}
		});
	}

	setResultadoSorteo(juegoSorteado: JuegoSorteado) {
		this.resultadoJuego = juegoSorteado;
	}

	get faltaVisualizarNumeros() {
		return this.resultadoJuego?.numerosSorteados.some(x => !x.visualizado);
	}

	obtenerCoordenasSiguienteEfecto() {
		for (let index = 0; index < this.resultadoJuego.numerosSorteados.length; index++) {
			if (!this.resultadoJuego.numerosSorteados[index].visualizado) {
				return this.coordenasCuadroGanador[index];
			}
		}
		return null;
	}

	obtenerNumeroSorteado() {
		const numeroSeleccionado = this.resultadoJuego.numerosSorteados.find(x => !x.visualizado);
		return numeroSeleccionado.numero;
	}

	obtenerCuadroGanador(): Phaser.GameObjects.Image {
		const numeroSeleccionado = this.resultadoJuego.numerosSorteados.findIndex(x => !x.visualizado);
		const cuadroGanador = {
			0: () => this.cuadroGanadorArriba,
			1: () => this.cuadroGanadorMedio,
			2: () => this.cuadroGanadorAbajo
		};
		return cuadroGanador[numeroSeleccionado]();
	}

	setVisualizarNumeroSorteado() {
		const numeroSeleccionado = this.resultadoJuego.numerosSorteados.findIndex(x => !x.visualizado);
		this.resultadoJuego.numerosSorteados[numeroSeleccionado].visualizado = true;
		if (numeroSeleccionado === 0) {
			this.numeroGanadorArriba = this.escena.add.image(this.coordenasCuadroGanador[numeroSeleccionado].x + 12, this.coordenasCuadroGanador[numeroSeleccionado].y + 12, AtlasImagenes.Bolas, AtlasImagenesDetalles[AtlasImagenes.Bolas].callbackObtenerFrame(this.resultadoJuego.numerosSorteados[numeroSeleccionado].numero))
				.setDepth(this.manejarDepth.NumeroGanador)
				.setOrigin(0, 0)
				.setDisplaySize(60, 60);
		} else if (numeroSeleccionado === 1) {
			this.numeroGanadorMedio = this.escena.add.image(this.coordenasCuadroGanador[numeroSeleccionado].x + 2, this.coordenasCuadroGanador[numeroSeleccionado].y + 12, AtlasImagenes.Bolas, AtlasImagenesDetalles[AtlasImagenes.Bolas].callbackObtenerFrame(this.resultadoJuego.numerosSorteados[numeroSeleccionado].numero))
				.setDepth(this.manejarDepth.NumeroGanador)
				.setOrigin(0, 0)
				.setDisplaySize(60, 60);
		} else if (numeroSeleccionado === 2) {
			this.numeroGanadorAbajo = this.escena.add.image(this.coordenasCuadroGanador[numeroSeleccionado].x + 14, this.coordenasCuadroGanador[numeroSeleccionado].y + 10, AtlasImagenes.Bolas, AtlasImagenesDetalles[AtlasImagenes.Bolas].callbackObtenerFrame(this.resultadoJuego.numerosSorteados[numeroSeleccionado].numero))
				.setDepth(this.manejarDepth.NumeroGanador)
				.setOrigin(0, 0)
				.setDisplaySize(60, 60);
			// this.estrellaRoja.anims.stopOnFrame(this.estrellaRoja.anims.currentAnim.frames[0]);
			this.estrellaRoja.anims.stop();
			this.estrellaRoja.setVisible(false);

			this.numeroGanador = this.escena.add.sprite(this.x + 175, this.y + 175, AtlasImagenes.Bolas, AtlasImagenesDetalles[AtlasImagenes.Bolas].callbackObtenerFrame(this.resultadoJuego.sumaNumeros))
				.setAlpha(0)
				.setDepth(this.manejarDepth.Estrella)
				.setDisplaySize(160, 160);

			this.escena.tweens.add({
				targets: this.numeroGanador,
				alpha: 1,
				duration: 1000,
				onStart: () => {
					//
				},
				onComplete: () => {
					//
				}
			});

		}
	}

